.map {
  display: flex;
  justify-content: space-between;
  column-gap: 30px;

  padding-top: 120px;
  padding-bottom: 120px;

  @include vp-1023() {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767() {
    flex-direction: column-reverse;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.map__wrapper {
  width: 560px;
  height: 306px;

  @include vp-1023() {
    width: 324px;
    height: 335px;
  }

  @include vp-767() {
    width: 100%;
    height: 316px;
    margin-bottom: 65px;
  }
}

.map__info-container {
  max-width: 500px;
  margin: 75px 0 40px;

  @include vp-1023() {
    max-width: 324px;
    margin: 65px 0 40px;
  }

  @include vp-767() {
    max-width: fit-content;
    margin: 55px 0 0;
  }
}

.map h3 {
  margin: 0 0 30px;
  padding: 0;

  font-family: "Montseratt", "Arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;

  @include vp-1023() {
    margin: 0 0 30px;
  }
}

.map__adress {
  position: relative;
  margin: 0 0 20px;
  padding: 0;

  font-family: "Montseratt", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  svg {
    margin-right: 10px;
    position: relative;
    top: 2px;
  }

  @include vp-1023() {
    position: relative;
    max-width: 240px;
    margin-left: 30px;

    svg {
      position: absolute;
      left: -28px;
      top: 5px;
     }
  }
}

.map__phone {
  margin: 0;
  padding: 0;

  font-family: "Montseratt", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  svg {
    margin-right: 10px;
    position: relative;
    top: 2px;
  }

  @include vp-767() {
    margin-bottom: 35px;
  }
}
